/* Animation for side bar slide in  */
@-webkit-keyframes sideBarSlider {
  0% {
    transform: translateX(-50%);
    opacity: 0;
  }

  150% {
    transform: translateX(100%);
    opacity: 1;
  }
}
@keyframes sideBarSlider {
  0% {
    transform: translateX(-50%);
    opacity: 0;
  }

  150% {
    transform: translateX(100%);
    opacity: 1;
  }
}

