    align-items: center;
    text-align: center;
    border-radius: 0px 0px 200px 200px / 0px 0px 175px 175px
}

.title, .subtitle{
    width: 60%;
    margin: auto;
}

.title{
    font-size: 3rem;
    line-height: normal;
    line-height: initial;
    padding: var(--space);
}

.subtitle {
    font-size: 18px;
}

#main-title {
    font-family: 'Caveat', cursive;
    font-size: 110px;
}

.boxes{
    position: absolute;
    top: 63vh;
    right: 0;
    width: 100%;
    display: flex;
    justify-content: center;
}

.box{
    padding: 2rem;
    background: white;
    margin: 0 3rem;
    min-height: 15rem;
    width: 18rem;
    border-radius: 15px;
    color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 0px 5px rgba(0,0,0,.6);
    transition: all 0.3s ease;
}

.box h3{
    font-size: 1rem;
    padding: .5rem 0;
}

.box:hover
{
    transform: scale(1.3);
    box-shadow:
        1px 1px rgba(76, 192, 61),
        2px 2px rgba(76, 192, 61),
        3px 3px rgba(76, 192, 61);
}

@media screen and (max-width: 900px){
    .boxes{
        flex-direction: column;
        padding: 1.5rem;
    }
    .box{
        width: 100%;
        margin: var(--space);
    }
}

@media screen and (max-width: 600px){
    :root{
        font-size: 14px;
    }
}
.overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 45%;
    opacity: 0;
    transition: .5s ease;
    background-color: #008bba93;
}
  
.img-tag:hover .overlay {
    opacity: 1;
}
  
.text {
    color: white;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}
.card-text p {
    display: inline;
}

img {
    max-width: 100%;
}

.hiddenText {
    display: none;
}

.modal-backdrop.show {
    opacity: 0;
    display: none;
}

#modal-content {
    margin: auto;
    margin-top: 0%;
    border: 1px solid #888;
    width: 100%;
    background-color: white;
}

#exampleModalid {
    height: 17%;
    margin-top: 20%;
